import streams from '../apis/streams'
import {
    SIGN_IN,
    SIGN_OUT,
    CREATE_STREAM,
    FETCH_STREAMS,
    FETCH_STREAM,
    DELETE_STREAM,
    EDIT_STREAM
} from './types'
//history object imported to allow programatic routing
import history from '../history'

const pushHistoryVar = '/#/'

export const signIn = (userId) => {
    return {
        type: SIGN_IN,
        payload: userId
    }
}

export const signOut = () => {
    return {
        type: SIGN_OUT
    }
}

export const createStream = (formValues) => {
    return async (dispatch, getState) => {
            // We get the userID and add it to the list record so we can later know who created the record
        const { userId } = getState().auth
        const response = await streams.post('/streams', {...formValues, userId})
        dispatch({
            type: CREATE_STREAM,
            payload: response.data
        })
        //Do some programmatic navigation to get user back to root route to list of streams
        history.push({pushHistoryVar})
    }
}

export const fetchStreams = () => {
    return async (dispatch) => {
        const response = await streams.get('/streams')
        dispatch({
            type: FETCH_STREAMS,
            payload: response.data
        })
    }
}

export const fetchStream = (id) => {
    return async (dispatch) => {
        const response = await streams.get(`/streams/${id}`)
        dispatch({
            type: FETCH_STREAM,
            payload: response.data
        })
    }
}

export const editStream = (id, formValues) => {
    return async (dispatch) => {
        const response = await streams.patch(`/streams/${id}`, formValues)
        dispatch({
            type: EDIT_STREAM,
            payload: response.data
        })
        //Do some programmatic navigation to get user back to root route to list of streams
        history.push({pushHistoryVar})
    }
}

export const deleteStream = (id) => {
    return async (dispatch) => {
        await streams.delete(`/streams/${id}`)
        dispatch({
            type: DELETE_STREAM,
            payload: id
        })
        //Do some programmatic navigation to get user back to root route to list of streams
        history.push({pushHistoryVar})
    }

}